<template>
    <div class="row" >
        <div class="col-lg-12">
            <div class="card">
                
                <div class="card-body" >
                    <div class="row">
                        <div class="col-md-12">
                            <h3>
                                Cadastro de Contas Recorrente  
                                <span 
                                    v-if="form.id" 
                                    class="float-right badge px-4 py-2"
                                    :class="{
                                        'badge-primary': form.ativo,
                                        'badge-secondary': !form.ativo,
                                    }"
                                >
                                    {{form.ativo ? "Ativo" : "Inativo"}}
                                </span>
                            </h3>
                            <hr>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-md-3">
                            <div class="nav flex-column nav-pills" id="v-pills-tab" role="tablist" aria-orientation="vertical">
                                <a class="nav-link mb-2 active" id="tab-cadastro-tab" data-toggle="pill" href="#tab-cadastro" role="tab" aria-controls="tab-cadastro" aria-selected="false">Cadastro</a>
                                <a @click="buscarLancamentos(); buscarSaldo()" class="nav-link mb-2" id="tab-lancamentos-tab" data-toggle="pill" href="#tab-lancamentos" role="tab" aria-controls="tab-lancamentos" aria-selected="true">Contas Geradas</a>
                            </div>
                        </div>
                        <div class="col-md-9">

                            <div class="tab-content text-muted mt-4 mt-md-0" id="v-pills-tabContent">
                                <form @submit.prevent="submit()" class="tab-pane fade active show" id="tab-cadastro" role="tabpanel" aria-labelledby="tab-cadastro-tab">
                        
                        <div class="col-md-12">
                            
                            <div class="row">
                                <div class="col-md-12">
                                    <h4>Dados Cadastrais</h4>
                                </div>
                            </div>
                            <hr>
                            <div class="row">
                                <label class="col-sm-3 col-form-label" v-show="form.id">Id</label>
                                <div class="col-sm-9" v-show="form.id">
                                    <input-componente
                                        :disabled="true"
                                        v-model="form.id"                            
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Fornecedor</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.parceiro_id">
                                        <vue-select :options="parceiros" placeholder="Comece a digitar para pesquisar" 
                                                @search="buscarParceiroDebounce" v-model="form.parceiro" label="apelido" 
                                                :clearable="false"
                                                @input="$v.form.parceiro_id.$touch(); form.parceiro_id = form.parceiro.id" >
                                            <div slot="no-options">Nenhum item para mostrar! Começe digitar para pesquisar</div>
                                        </vue-select>
                                    </container-componente>
                                </div>

                                <label class="col-sm-3 col-form-label">Descrição</label>
                                <div class="col-sm-9">
                                    <input-componente
                                        v-model="form.descricao"
                                        :validator="$v.form.descricao"
                                        @blur="$v.form.descricao.$touch"
                                        />
                                </div>

                                <label class="col-sm-3 col-form-label">Classificação</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.classificacao">
                                        <select class="form-control"
                                            placeholder="Selecione uma opção" v-model="form.classificacao"
                                            @input="$v.form.classificacao.$touch(); selecionouClassificacaoConta();" 
                                        >
                                            <option v-for="classificacao in classificacoes" :key="classificacao" :value="classificacao">{{classificacao}}</option>

                                        </select>
                                    </container-componente>
                                </div>

                                <label class="col-sm-3 col-form-label">Despesa</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.plano_conta_id">
                                        <vue-select 
                                                :options="planos_contas" 
                                                @input="form.plano_conta_id = form.plano_conta.id"
                                                :clearable="false" @search="buscarContaDespesaDebounce"
                                                v-model="form.plano_conta" label="nome" > 
                                            <div slot="no-options">Nenhum item para mostrar! Começe digitar para pesquisar</div>
                                        </vue-select>                                       
                                    </container-componente>
                                </div>

                                <label class="col-sm-3 col-form-label">Centro de Custo</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.centro_custo_id">
                                        <vue-select 
                                                :options="centros_custo" 
                                                @input="form.centro_custo_id = form.centro_custo.id"
                                                :clearable="false" @search="buscarCentroCustoDebounce"
                                                v-model="form.centro_custo" label="nome" > 
                                            <div slot="no-options">Nenhum item para mostrar! Começe digitar para pesquisar</div>
                                        </vue-select>                                       
                                    </container-componente>
                                </div>

                                <label class="col-sm-3 col-form-label">Qtde de Contas futuras</label>
                                <div class="col-sm-9">
                                <container-componente :validator="$v.form.qtde_futuras">
                                        <money  v-bind="inteiro" class="form-control" v-model.lazy="form.qtde_futuras" ></money>
                                    </container-componente>
                                </div>

                                <label class="col-sm-3 col-form-label">Periodicidade</label>
                                <div class="col-sm-9">
                                    <select class="form-control"
                                        v-model="form.periodicidade"
                                        :validator="$v.form.periodicidade"
                                        @input="$v.form.periodicidade.$touch"
                                    >
                                        <option value="MENSAL">MENSAL</option>
                                    </select>
                                </div>
                            </div>
                            <div class="row" v-if="!this.form.id">
                                <div class="col-sm-12">
                                    <hr>
                                </div>
                                <label class="col-sm-3 col-form-label">Valor Inicial</label>
                                <div class="col-sm-9">
                                   <container-componente :validator="$v.form.valor_inicial">
                                        <money v-bind="currency" class="form-control" @blur="$v.form.valor_inicial.$touch" v-model.lazy="form.valor_inicial" ></money>
                                   </container-componente>

                                </div>

                                <label class="col-sm-3 col-form-label">Data da Primeira Conta</label>
                                <div class="col-sm-9">
                                    <container-componente :validator="$v.form.vencimento_inicial">
                                        <date-picker :clearable="false" v-model="form.vencimento_inicial" :format="'DD/MM/YYYY'"  :disabledDate="desabilitaDataPassada"></date-picker>
                                   </container-componente>
                                </div>
                            </div>   
                            <div class="row" style="margin-top:20px" >
                                <div class="col-md-12 text-center" v-if="erros || sucesso">
                                    <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                                    <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                                </div>
                                <div class="col-md-12 button-items text-center">

                                    <button v-if="!form.ativo && form.id" type="button" class="btn btn-success waves-effect waves-light w-sm float-left" title="Ativar" @click="confirmar('Ativar')"
                                        data-toggle="modal" data-target="#modal-ativar"
                                    >
                                        <i class="bx bx bx-play-circle d-block font-size-24"></i> Ativar Conta
                                    </button>

                                    <button v-if="form.ativo  && form.id" type="button" class="btn btn-secondary waves-effect waves-light w-sm float-left" title="Desativar" @click="confirmar('Desativar')"
                                        data-toggle="modal" data-target="#modal-ativar"
                                    >
                                        <i class="bx bx bx-pause-circle d-block font-size-24"></i> Desativar Conta
                                    </button>


                                    <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'contasRecorrentesIndex'}">
                                        <i class="bx bx-arrow-back d-block font-size-24"></i> Voltar
                                    </router-link>

                                    <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                                        <i class="bx bxs-save d-block font-size-24"></i> Salvar
                                    </button>

                                </div>
                            </div>  
                        </div>
                    </form>
                            </div>
                        </div>
                    </div>

                    

                    
                </div>
                
                <div class="row">
                    <div class="col-md-12 text-right" v-if="form.id">
                        <log-componente :index="form.id" subject_type="App\Models\Cliente\ContaRecorrente"  :subject_id="form.id" ></log-componente>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="modal-ativar" tabindex="-1" aria-labelledby="modal-ativar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-dialog-scrollable">
                <div class="modal-content" style="min-height: 300px" >
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-ativarTitle"> {{acao}} conta </h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body" >
                        <div class="row">
                            <div class="col-sm-12">
                                <h4>Tem certeza que deseja {{acao}} esta conta?</h4>
                            </div>

                            <div class="col-sm-12 form-group" v-if="acao === 'Ativar'">
                                Esta ação vai gerar {{form.qtde_futuras}} contas futuras pendentes de pagamento!
                            </div>

                            <div class="col-sm-6 form-group" v-if="acao === 'Ativar'">
                                <container-componente label="Vencimento Inicial" :validator="$v.form.vencimento_inicial">
                                    <date-picker :clearable="false" v-model="form.vencimento_inicial" :format="'DD/MM/YYYY'"></date-picker>
                                </container-componente>
                            </div>

                            <div class="col-sm-6 form-group" v-if="acao === 'Ativar'">
                                <container-componente label="Valor Inicial" :validator="$v.form.valor_inicial">
                                    <money v-bind="currency" class="form-control" @blur="$v.form.valor_inicial.$touch" v-model.lazy="form.valor_inicial" ></money>
                                </container-componente>
                            </div>

                            <div class="col-sm-12 form-group" v-if="acao === 'Desativar'">
                                Esta ação vai cancelar todas as contas futuras pendentes de pagamento!
                            </div>
                        </div>
                        <div class="row" v-if="sucessoAtivar || erroAtivar">
                            <div class="col-sm-12">
                                <mensagem-componente v-if="erroAtivar" :erros="erroAtivar" @fechar="erroAtivar = null"></mensagem-componente>
                                <mensagem-componente v-if="sucessoAtivar" :sucesso="sucessoAtivar" @fechar="sucessoAtivar = null"></mensagem-componente>
                            </div>
                        </div>

                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary waves-effect waves-light w-sm float-left" data-dismiss="modal">  <i class="bx bx bx bxs-caret-left-circle d-block font-size-24"></i> Voltar</button>
                        
                        <button :disabled="sucessoAtivar" v-if="acao === 'Ativar'" type="button" class="btn btn-success waves-effect waves-light w-sm float-left" title="Ativar" @click="ativar()">
                            <i class="bx bx bx-play-circle d-block font-size-24"></i> Ativar Conta
                        </button>

                        <button :disabled="sucessoAtivar" v-if="acao === 'Desativar'" type="button" class="btn btn-warning waves-effect waves-light w-sm float-left" title="Desativar" @click="desativar()">
                            <i class="bx bx bx-pause-circle d-block font-size-24"></i> Desativar Conta
                        </button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

import { required, requiredIf,  maxLength, minValue, maxValue } from "vuelidate/lib/validators";
import { ContasRecorrentesService, CentroCustoService, PlanoContasService, ParceiroService } from '../../../services'

const contasRecorrentesService = new ContasRecorrentesService();
const centroCustoService = new CentroCustoService();
const planoContasService = new PlanoContasService();
const parceiroService = new ParceiroService();

export default {
  data() {
        return {
            form: {
                id: null,
                descricao: '',
                parceiro_id: '',
                qtde_futuras: 12,
                periodicidade: 'MENSAL',
                classificacao: '',
                plano_conta_id: '',
                centro_custo_id: '',
                ativo: false,

                parceiro: null,
                plano_conta: null,
                centro_custo: null,

                vencimento_inicial: null,
                valor_inicial: 0,
            },
            acao: 'Desativar',
            sucessoAtivar: null,
            erroAtivar: null,
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
              
            timer: null,
            centros_custo: [],
            parceiros: [],
            planos_contas: [],

            periodicidades: ['SEMANAL,MENSAL,BIMESTRAL,TRIMESTRAL,SEMESTRAL,ANUAL'],
            classificacoes: ['ADMINISTRATIVA','OPERACIONAL','COMISSAO','OUTROS','PESSOAL'],            
            inteiro: {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 0,
                masked: false
            }

        } ;
    },
    validations() {
        return {
            form: {                
                descricao: { required, maxLength:maxLength(200)},
                parceiro_id: {required},
                qtde_futuras: {required, minValue: minValue(1), maxValue: maxValue(60)},
                periodicidade: {required},
                classificacao: {required},
                plano_conta_id: {required},
                centro_custo_id: {required},

                vencimento_inicial: {required: requiredIf( () => !this.form.id)},
                valor_inicial: {required: requiredIf( () => !this.form.id), minValue: minValue(0.01)},
            },
        }
        

    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }
        this.buscarCentroCusto();

    },
    methods: {

        submit: function(){
            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            
            delete obj.ativo
            delete obj.parceiro
            delete obj.plano_conta
            delete obj.centro_custo
            
            let request = null;
            if(obj.id)    
                request = contasRecorrentesService.atualizar(obj);
            else
                request = contasRecorrentesService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        this.form.id = response.data.id;
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            contasRecorrentesService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    this.form.valor_inicial = Number(this.form.valor_inicial)
                }).catch( erro => this.erros = erro)
        },

        buscarParceiroDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarParceiro(text, loading)
            }, 800);
            
        },
        buscarParceiro: function(text, loading){
            let filtro = {texto: text, per_page:40};
            parceiroService.listar(filtro).then( response => this.parceiros = response.data.data)
        },

        selecionouClassificacaoConta: function(){
            this.form.plano_conta_id = '';
            this.form.plano_conta = null;
            setTimeout(() => {
                this.buscarContaDespesa();
            }, 50);
        },
        
        buscarContaDespesaDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarContaDespesa(text, loading)
            }, 800);
            
        },

        buscarContaDespesa: function(text, loading){
            if(!this.form.classificacao)
                return
            let filtro = {tipo: this.form.classificacao, texto: text, per_page:40};
            
            planoContasService.listar(filtro).then(
                response => this.planos_contas = response.data.data.map(x => {
                    x.nome = x.codigo + ' - ' + x.nome;
                    return x;
                })
            );
        },

        buscarCentroCustoDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarCentroCusto(text, loading)
            }, 800);
            
        },
         buscarCentroCusto: function(text, loading){
           
            let filtro = {texto: text, per_page:40};
            
            centroCustoService.listar(filtro).then(
                response => this.centros_custo = response.data.data.map(x => {
                    x.nome = x.codigo + ' - ' + x.nome;
                    return x;
                })
            );
        },

        confirmar(acao){
            this.sucessoAtivar = null;
            this.erroAtivar = null;
            this.acao = acao;
            if(acao == 'Ativar'){
                this.form.vencimento_inicial = new Date();
            }
        },

        ativar(){
            this.sucessoAtivar = null;
            this.erroAtivar = null;

            this.$v.form.$touch();
            if (this.$v.form.$invalid){
                this.erroAtivar = this.$v.form;
                return false;
            }
            let obj = {
                valor_inicial: this.form.valor_inicial,
                vencimento_inicial: this.form.vencimento_inicial
            }

            contasRecorrentesService.ativar(this.form.id, obj).then(
                response => {
                    this.form.ativo = response.data.ativo
                    this.sucessoAtivar = 'Conta ativada com sucesso';
                }).catch(   erro => this.erroAtivar = erro);
        },
        desativar(){
            this.sucessoAtivar = null;
            this.erroAtivar = null;

            contasRecorrentesService.desativar(this.form.id).then(
                response => {
                    this.form.ativo = response.data.ativo
                    this.sucessoAtivar = 'Conta desativada com sucesso';
                }).catch(   erro => this.erroAtivar = erro);
        },

        desabilitaDataPassada: function(data) {
            let hoje = new Date();
            return data < hoje
        },
    },

    computed: {
        currency(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            }
        }
    }
};
</script>
<style scoped>

</style>